$('.submenu-open').bind('click', function(e){
  jQuery(this).parent().find('.hide').slideToggle('fast');  // apply the toggle to the ul
  jQuery(this).parent().toggleClass('show');
  e.preventDefault();
});

// Sidebar Hide Show
var sidebar = function(){
    // auto close sidebar
    if ( $(window).width() < 1210 && $(window).width() >= 768 ) {
        $('.sidebar').removeClass('open').addClass('close-sm');
        $('.hamberger').addClass('active');
    }

    // sidebar on pad or desktop device
    if ( $(window).width() >= 768 ) {
        $('.hamberger').click(function(){
            $(this).addClass('disable');

            if ( $('.sidebar').hasClass('open') ) {
                $('.hamberger').addClass('active');
                $('.sidebar-content').addClass('transparent', 400);
                $('.page-dashboard').addClass('page');

                $('.sidebar').delay(150).addClass('close-sm', 600).removeClass('open', 600);
            } else {
                $('.hamberger').removeClass('active');
                $('.sidebar-content').removeClass('transparent', 800);
                $('.sidebar').addClass('open', 600);
                $('.page-dashboard').removeClass('page');
                $('.sidebar').removeClass('close-sm', 600);
            }

            setTimeout(function(){
                $('.hamberger').removeClass('disable');
            }, 1200);
        });
    // sidebar on mobile device
    } else {
        $('.sidebar').removeClass('open').removeClass('close-sm').addClass('close-xs').addClass('sidebar-xs');
        $('.hamberger').click(function(){
            if ( $('.sidebar').hasClass('close-xs') ) {
                $('.sidebar').removeClass('close-xs', 800).addClass('open-xs');
            } else {
                $('.sidebar').removeClass('open-xs').addClass('close-xs', 800);
            }
        });
    }
 }

    $(window).resize(function(){
        $(this).addClass('disable');

        // 768 <= resize size < 1210
        if ( $(window).width() < 1210 && $(window).width() >= 768 ){
            $('.hamberger').addClass('active');
            $('.page-dashboard').addClass('page');
            $('.sidebar-content').addClass('transparent', 400);
            $('.sidebar').delay(150).addClass('close-sm', 600).removeClass('open', 600);
        }

        // resize size >= 1210
        else if( $(window).width() >= 1210 ) {
            $('.hamberger').removeClass('active');
            $('.page-dashboard').removeClass('page');
            $('.sidebar-content').removeClass('transparent', 800);
            $('.sidebar').addClass('open', 600);
            $('.sidebar').removeClass('close-sm', 600);
        }

        // resize size < 768
        else{
        }
        setTimeout(function(){
            $('.hamberger').removeClass('disable');
        }, 1200);
    });


$(document).ready(function(){
  sidebar();
});

